import { DateFormat } from "./../../date-format";
import { Http } from "@angular/http";
import { AmazonSrvService } from "./../../Services/AmazonSrv/amazon-srv.service";
import { Component, OnInit, NgModule } from "@angular/core";
import { CacheSrvService } from "./../../Services/CacheSrv/cache-srv.service";
import { DateAdapter } from "@angular/material";
import { Input } from "@angular/core/src/metadata/directives";
import { SelectItem, TreeNode } from "primeng/primeng";
import { forEach } from "@angular/router/src/utils/collection";
@Component({
  selector: "app-lista-padaria",
  templateUrl: "./lista-padaria.component.html",
  styleUrls: ["./lista-padaria.component.scss"],
  providers: [AmazonSrvService]
})
export class ListaPadariaComponent implements OnInit {
  public relatorios: any = [];
  teste: any = [];
  titulos: any = []
  AWS_Relatorio = {
    aplicacao: null,
    reporte: null,
    anos: null,
    meses: null,
    arquivos: null
  };
  show1: boolean = false
  show2: boolean = false
  constructor(
    public cacheSrv: CacheSrvService,
    private dateAdapter: DateAdapter<Date>,
    private awsService: AmazonSrvService,
    private http: Http
  ) {
    dateAdapter.setLocale('en-in');
    this.titulos = [
      {
        id: 1,
        titulo: '1. Quantidade de Usurio',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false
      },
      {
        id: 2,
        titulo: '2. Quantidade de publicaes por usurio',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false

      },
      {
        id: 3,
        titulo: '3. Quantidade de publicaes por perfil',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false
      },
      {
        id: 4,
        titulo: '4. Quantidade de visualizaes por perfil',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false
      },
      {
        id: 5,
        titulo: '5. Quantidade de curtidas por publicao',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false
      },
      {
        id: 6,
        titulo: '6. Quantidade de videos assistidos por dia e faixa de horrio',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false
      },
      {
        id: 7,
        titulo: '7. Quantidade de visualizaes por vdeo',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false
      },
      {
        id: 8,
        titulo: '8. Quantidade geral de acessos, independente da interao',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false
      },
      {
        id: 9,
        titulo: '9. Quantidade de interaes por dia e faixa de horrio das publicaes',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false

      },
      {
        id: 10,
        titulo: '10. Geral',
        Seta1: null,
        Seta2: null,
        show1: false,
        show2: false
      }
    ]
    this.cacheSrv.componentMethodCalled$.subscribe(
      () => {
        this.getRelatorio();
      })
  }
  ngOnInit(): void {
    this.getRelatorio()
  }
  iniciarRotacao(item, seta) {
    var rotate
    if (seta == 1) {
      if (item.Seta1 == null || item.Seta1 == 'rotateToClose') {
        item.Seta1 = 'rotateToOpen'
        item.show1 = true
        
      } else {
        if (item.Seta1 == 'rotateToOpen') {
          item.Seta1 = 'rotateToClose'
          item.show1 = false
        }
      }
    }else{
      if (item.Seta2 == null || item.Seta2 == 'rotateToClose') {
        item.Seta2 = 'rotateToOpen'
        item.show2 = true
      } else {
        if (item.Seta2 == 'rotateToOpen') {
          item.Seta2 = 'rotateToClose'
          item.show2 = false
        }
      }
    }
  }

  getRelatorio() {
    this.awsService.listarRelatorios()
      .then((res) => {
        this.relatorios = res.filter((r) => r.application == 2)[0].reports;
      });
  }

}


