import { environment } from './../../../environments/environment';
import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import { log } from 'util';
import { CacheSrvService } from '../CacheSrv/cache-srv.service';
import { HttpClient } from '@angular/common/http';

@Injectable()



export class AmazonSrvService {
  data: any
  linkGetRelatorio = environment.linkGetRelatorio
  linkLoginService = environment.linkLoginService
  linkGenerateRelatorio = environment.linkGenerateRelatorio

  constructor(private http: Http, public cacheSrv: CacheSrvService) { }

  private x_api_key = 'U4exZaLqkX8vQatRYqRxk5r3dHKtYtm82EG5b5uN'
  private x_amz = '';
  private authorization = 'Basic ' + this.x_amz;
  private identification_app = '510l8aje58njfc0mc77hd11pva';
  private dateCurrent = new Date();

  private getHeader() {
    let headers = new Headers();

    headers.append('Content-Type', 'application/json');
    headers.append('x-amz-date', this.dateCurrent.toString());
    headers.append('Authorization', this.authorization);
    headers.append('x-api-key', this.x_api_key);
    headers.append('x-amz-security-token', this.x_amz);
    headers.append('clientid', this.identification_app);
    headers.append('x-application', "3");
    return new RequestOptions({ headers: headers });
  }
  private getHeaderLogin() {
    let headers = new Headers();

    headers.append('Content-Type', 'application/json');
    headers.append('x-amz-date', this.dateCurrent.toString());
    headers.append('Authorization', this.authorization);
    headers.append('x-api-key', this.x_api_key);
    headers.append('x-amz-security-token', this.x_amz);
    return new RequestOptions({ headers: headers });
  }
  listarRelatorios() {
    return this.http.get(this.linkGetRelatorio, this.getHeader())
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }
  VerificarLogin(username, senha) {
    this.data = {
      "username": username,
      "password": senha,
      "appVersion": "1.2.2",
      "osType": "IOS",
      "registrationId": "",
      "params": { "header": { "x-application": "3" } },
      "context": {
        "stage": environment.stage
      }
    }
    return this.http.post(this.linkLoginService, this.data, this.getHeaderLogin())
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError);
  }


  gerarRelatorio(params) {
    let objectParams: any = {};
    Object.entries(params).forEach(([key, value]) => {
      if (key == 'startMonth') {
        objectParams.startMonth = params.startMonth.format('YYYY-MM-DD');
      } else
        if (key == 'lastMonth') {
          objectParams.lastMonth = params.lastMonth.format('YYYY-MM-DD');
        } else {
          objectParams[key] = value; // "a 5", "b 7", "c 9"     
        }
      if (key == 'month') {
        objectParams.month = params.month.format('YYYY-MM-DD');
      }
      if (key == 'startTime') {
        objectParams.startTime = params.startTime.toLocaleTimeString();
      }
      if (key == 'endTime') {
        objectParams.endTime = params.endTime.toLocaleTimeString();
      }
    });
    this.data = objectParams;
    console.log('Parametros', this.data)
    //reportId
    //application
    return this.http.post(this.linkGenerateRelatorio, this.data, this.getHeader())
      .toPromise()
      .then(this.extractData)
      .catch(this.handleError)
  }
  // metodos privados
  private extractData(res: Response) {
   // console.clear()
    const body = res.json();
    //console.log('Extratc data',body)
    return body || {};
  }
  private handleError(error: any): Promise<any> {
    
    return Promise.reject(error.message || error);
  }

}









