  toasterText: string;
  Logado: boolean = false;
  ValidarParametros = { MesRef: true, FaixaHora: true, PeriodoDate: true }
  isSucesss: boolean
  @ViewChildren(DatePickerComponent) datePicker: QueryList<DatePickerComponent>;
  datePickerArr = [];
  constructor(public cacheSrv: CacheSrvService, private awsService: AmazonSrvService) {
    this.itemDabase()
  }
  ngAfterViewInit() {
    this.datePickerArr = this.datePicker.toArray();
  }
  ngOnInit() {
  }
  modelChanged(event) {
    console.log('MODEL CHANGERD!', event);
  }
