import { CacheSrvService } from './../../Services/CacheSrv/cache-srv.service';
import { AmazonSrvService } from './../../Services/AmazonSrv/amazon-srv.service';
import { Component, OnInit } from '@angular/core';



@Component({
  selector: 'app-history-page',
  templateUrl: './history-page.component.html',
  styleUrls: ['./history-page.component.scss']
})
export class HistoryPageComponent implements OnInit {


  constructor(public awsSrv:AmazonSrvService, public cacheSrv:CacheSrvService) { }

  ngOnInit() {}

  changeTab($event){  
    this.cacheSrv.callComponentMethod();
    this.awsSrv.listarRelatorios()
  }  
}





