import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'reportFilter'
})
export class ReportFilterPipe implements PipeTransform {

  transform(items: any[], args?: any): any {
    if(!items || !args) {
      return items;
    }

    return items.filter((i) => i.reportId == args);


  }

}



