import { Title } from '@angular/platform-browser';
import { StorageService } from './../../Services/storage.service';
import { CacheSrvService } from './../../Services/CacheSrv/cache-srv.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import swal from 'sweetalert2';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  nameLocalStorageSessionLogin = 'token_user';
  titulo: any;
  public ifLogged = false;

  constructor(
    public cacheSrv: CacheSrvService,
    private storage: StorageService,
    private router: Router
  ) { }

  ngOnInit() {
    this.titulo = this.storage.readSessionStorage('tituloObj')
    let token = this.storage.readSessionStorage('token_user')
    if (token) {
      this.ifLogged = true;
    }
  }

  logoff() {
   // this.loading(true);
    this.storage.eraseSessionStorage('token_user');
    this.router.navigate(['/home']);
  }


  private loading(statusModal: any) {
    if (statusModal) {
      swal({
        showConfirmButton: false,
        showCancelButton: false,
        allowEscapeKey: false,
        allowOutsideClick: false,
        title: '<div style="font-size:17px; text-align: center;  color: #7CA0B2" >Logout...</div>',
        background: '#D9EDF6'
      });
      swal.showLoading();
    } else {
      swal.hideLoading();
      swal.close();
    }
  }

}



