import { BrowserModule } from "@angular/platform-browser";
import { NgModule } from "@angular/core";
import { RouterModule } from "@angular/router";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { AppComponent } from "./app.component";
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
//NGX Bootstrap

//Paginas
  import { FilterPageComponent } from "./Pages/filter-page/filter-page.component";
  import { HistoryPageComponent } from "./Pages/history-page/history-page.component";
  import { MainPageComponent } from "./Pages/main-page/main-page.component";
  import { LoginPageComponent } from "./Pages/login-page/login-page.component";
//Servios
  import { AppRoutingModule } from "./app.routing.module";
  import { CacheSrvService } from "./Services/CacheSrv/cache-srv.service";
  import { AmazonSrvService } from "./Services/AmazonSrv/amazon-srv.service";
  import { HttpModule } from "@angular/http";
  import { StorageService } from "./Services/storage.service";
  import { HttpClientModule } from "@angular/common/http";
//Componentes
  import { HeaderComponent } from "./Components/header/header.component";
  import { TabsComponent } from "./Components/tabs/tabs.component";
  import { FooterComponent } from "./Components/footer/footer.component";
  import { ListaJetOilComponent } from "./Components/lista-jet-oil/lista-jet-oil.component";
  import { ListaPadariaComponent } from "./Components/lista-padaria/lista-padaria.component";
  import { ListaBeerCaveComponent } from "./Components/lista-beer-cave/lista-beer-cave.component";
//AngularMaterial
  import {
    MatButtonModule,
    MatCardModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatExpansionModule,
    MatTabsModule,
    MatToolbarModule,
  } from "@angular/material";
  import { CdkTableModule } from "@angular/cdk/table";
//DatePickers
  import { DpDatePickerModule } from "ng2-date-picker";
//PrimeNG
  import {CalendarModule} from 'primeng/calendar';
  import {ProgressBarModule} from 'primeng/progressbar';
//PIPE
  import { ReportFilterPipe } from "./pipes/report-filter.pipe";
  import { PipeSort } from './pipes/pipe-sort.pipe';

@NgModule({
  declarations: [
    AppComponent,
    MainPageComponent,
    LoginPageComponent,
    HistoryPageComponent,
    FilterPageComponent,
    HeaderComponent,
    TabsComponent,
    FooterComponent,
    ListaJetOilComponent,
    ListaPadariaComponent,
    ListaBeerCaveComponent,
    ReportFilterPipe,
    PipeSort
  ],
  entryComponents: [
    HeaderComponent,
    TabsComponent,
    FooterComponent,
    ListaJetOilComponent,
    ListaPadariaComponent,
    ListaBeerCaveComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    BrowserAnimationsModule,
  //Ng's
  //DatePickers
    DpDatePickerModule,
  //AngularMaterial
    CdkTableModule,
    MatButtonModule,
    MatCardModule,
    MatDatepickerModule,
    MatExpansionModule,
    MatNativeDateModule,
    MatTabsModule,
    MatToolbarModule,
    
  //PrimeNG
    CalendarModule,
    ProgressBarModule,
  //Http
    HttpClientModule,
    HttpModule,
    AppRoutingModule
  ],
  providers: [CacheSrvService, AmazonSrvService, StorageService, {provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule {}



