import { StorageService } from './../../Services/storage.service';
import { CacheSrvService } from './../../Services/CacheSrv/cache-srv.service';
import { Component, OnInit } from '@angular/core';
import { RouterModule, Router } from '@angular/router';
import { AmazonSrvService } from '../../Services/AmazonSrv/amazon-srv.service';
import swal from 'sweetalert2';
import { environment } from './../../../environments/environment';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.scss']
})
export class LoginPageComponent implements OnInit {
  nameLocalStorageSessionLogin = 'token_user';
  credencials = {
  usuario: environment.usuario,
    senha: environment.senha
  };
  constructor(
    private router: Router,
    private awsSrv: AmazonSrvService,
    private cacheSrv: CacheSrvService,
    private storage: StorageService
  ) { }

  ngOnInit() {}
  Login() {
    this.Loading(true);
    this.storage.writeSessionStorage(this.credencials.usuario, 'tituloObj')
    this.cacheSrv.SetTitulo(this.credencials.usuario)
    this.awsSrv.VerificarLogin(this.credencials.usuario, this.credencials.senha)
      .then((res) => {
        console.log('Aqui esta o Res',res)
        if(res.errorMessage) {
          this.LoginError();
          this.storage.ClearSessionStorage();
        } else {
          this.storage.writeSessionStorage(JSON.stringify(res), this.nameLocalStorageSessionLogin);
          if (this.storage.readSessionStorage(this.nameLocalStorageSessionLogin)) {
           this.Loading(false);
            
            this.router.navigate(['/home']);
          }
        }
      })
      .catch((err) => console.log('error: ',err));
    }
 private Loading(statusModal: any) {
    if (statusModal) {
      swal({
        showConfirmButton: false,
        showCancelButton: false,
        allowEscapeKey: false,
        allowOutsideClick: false,
        background: '#D9EDF6'
      });
      swal.showLoading();
    } else {
      swal.hideLoading();
      swal.close()
    }
  }  
  private LoginError(){   
      swal({
        html:`<div  style=" font-family: 'Ubuntu', serif; color: #B83740;" > <span style="font-weight: bold;">Erro!</span> Login ou senha invlidos</div>`,
        background: '#F2DEDF' ,
        position: 'top-right',
        showConfirmButton: false,
        showCloseButton: true,
        toast: true,
        width: 510
      });
    }
  }
 



