import { element } from 'protractor';
import { PipeTransform, Pipe } from '@angular/core';

@Pipe({ name: "sortBy" })

export class PipeSort implements PipeTransform {
	transform(array: any[], args: any): any[] {
		array.sort((a: any, b: any) => {
			var parts = a[args].lastModified
			var parts2 = b[args].lastModified
			const mydate1 = new Date(parts)
			const mydate2 = new Date(parts2)
			if (mydate1 > mydate2) {
				return -1;
			} else if (mydate1 < mydate2) {
				return 1;
			} else {
				return 0;
			}
		});
		return array;
	}
}


